<template>
  <v-snackbar
    :timeout="(time = 2000)"
    v-model="alert"
    top
    persistent
    multi-line
    color="blue lighten-2"
    dark
    icon="mdi-alert-circle-outline"
    transition="scale-transition"
    >완료되었습니다
    <v-btn flat color="blue lighten-2" @click="alertConfirm">확인</v-btn>
  </v-snackbar>
</template>
<script>
import axios from "axios";
import { EventBus } from "./eventBus.js";

export default {
  data() {
    return {
      alert: false
    };
  },

  methods: {
    alertConfirm() {
      location.href = "/profile";
    }
  },
  created() {
    EventBus.$on("EditSave", item => {
      this.alert = true;
    });
  }
};
</script>
